import React, { useState } from "react";
import { Globe, Github, Ellipsis, MoveRight, EllipsisVertical } from "lucide-react";

interface ArtifactOption {
  id: string;
  Icon: React.ElementType;
}

export function DrawerContent() {
  const [status, setStatus] = useState<"Active" | "Inactive">("Active");
  const [selectedIcon, setSelectedIcon] = useState<string | null>(null);

  const toggleStatus = () => {
    setStatus((prev) => (prev === "Active" ? "Inactive" : "Active"));
  };

  const artifactOptions: ArtifactOption[] = [
    { id: "github", Icon: Github },
    { id: "globe", Icon: Globe },
    { id: "ellipsis", Icon: Ellipsis },
  ];

  return (
    <div className="p-4">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-base font-semibold">Create New Project</h2>
        <MoveRight className="w-6 h-6 text-gray-700 cursor-pointer" />
      </div>
      <div className="border-t border-gray-200 mb-6"></div>
      <div className="flex justify-between items-center mb-6">
        <div
          className={`px-3 py-2 rounded-md text-sm cursor-pointer ${
            status === "Active"
              ? "bg-green-100 text-green-700"
              : "bg-red-100 text-red-700"
          }`}
          onClick={toggleStatus}
        >
          {status} <span className="ml-1">â–¼</span>
        </div>
        <EllipsisVertical className="w-5 h-5 text-gray-600 cursor-pointer" />
      </div>
      <div className="mb-4">
        <h3 className="text-md font-medium text-[#707070]-800">Project Details</h3>
      </div>
      <div className="mb-6">
        <h4 className="text-sm font-medium mb-6 text-gray-700">Choose Artifact</h4>
        <div className="flex justify-center space-x-12 items-center">
          {artifactOptions.map(({ id, Icon }) => (
            <div
              key={id}
              className={`w-16 h-16 flex justify-center items-center border rounded-lg cursor-pointer transition-all ${
                selectedIcon === id
                  ? "bg-indigo-100 text-indigo-600 shadow-md"
                  : "hover:bg-gray-100"
              }`}
              onClick={() => setSelectedIcon(id)}
            >
              <Icon className="w-8 h-8" />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
